import {Injectable} from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {
    NGXLoggerMonitor,
    NgxLoggerLevel,
    NGXLogInterface
} from 'ngx-logger';
import {GlobalSettingsService} from '@hrs/providers';
import {GatewayService} from '@hrs/gateway';
import {environment} from '@app/env';

/**
 * Hook into ngx-logger services:
 * allows custom treatment for log messaging on top of the base service's console logging support
 */

 @Injectable({
    providedIn: 'root',
})

export class HRSLoggerMonitor implements NGXLoggerMonitor {
    private static LOG_URL_KEY: string = 'loggingURL';
    private static LOG_LEVEL_KEY: string = 'loggingLevel';

    private loggingURL: string = '';    // server URL
    private loggingLevel: string = '';  // restriction level for server logging
    private ngxLoggingLevel: NgxLoggerLevel;    // restriction level converted to match NGXLoggerLevels
    private applicationName: string = '';

    private stringToLevel: {[key: string]: NgxLoggerLevel} = {
        'trace':   NgxLoggerLevel.TRACE,    // 0
        'debug':   NgxLoggerLevel.DEBUG,    // 1
        'info':    NgxLoggerLevel.INFO,     // 2
        'log':     NgxLoggerLevel.LOG,      // 3
        'warn':    NgxLoggerLevel.WARN,     // 4
        'error':   NgxLoggerLevel.ERROR,    // 5
        'fatal':   NgxLoggerLevel.FATAL,    // 6
        'off':     NgxLoggerLevel.OFF       // 7
    }

    private levelToString: {[key: string]: string} = {
        [NgxLoggerLevel.TRACE]:  'trace',     // 0
        [NgxLoggerLevel.DEBUG]:  'debug',     // 1
        [NgxLoggerLevel.INFO]:   'info',      // 2
        [NgxLoggerLevel.LOG]:    'log',       // 3
        [NgxLoggerLevel.WARN]:   'warn',      // 4
        [NgxLoggerLevel.ERROR]:  'error',     // 5
        [NgxLoggerLevel.FATAL]:  'fatal',     // 6
        [NgxLoggerLevel.OFF]:    'off'        // 7
    }

    constructor(
        private globalSettingsService: GlobalSettingsService,
        private gatewayService: GatewayService,
        private http: HttpClient
    ) {
        this.loggingURL = localStorage.getItem(HRSLoggerMonitor.LOG_URL_KEY);
        this.loggingLevel = localStorage.getItem(HRSLoggerMonitor.LOG_LEVEL_KEY);
        this.ngxLoggingLevel = this.stringToLevel[this.loggingLevel];
    }

    public init(app: string): void {
        this.applicationName = app;

        this.globalSettingsService.globalSettingsLoading.then(() => {
            let globalAttributes = this.globalSettingsService.getGlobalAttributes();
            this.loggingURL = globalAttributes['LOG_URL'];
            this.loggingLevel = globalAttributes['LOG_LEVEL'];
            this.ngxLoggingLevel = this.stringToLevel[this.loggingLevel];
            this.updateStorage();
        });
    };

    private updateStorage(): void {
        if (this.loggingURL) {
            localStorage.setItem(HRSLoggerMonitor.LOG_URL_KEY, this.loggingURL);
        } else {
            localStorage.removeItem(HRSLoggerMonitor.LOG_URL_KEY);
        }
        if (this.loggingLevel) {
            localStorage.setItem(HRSLoggerMonitor.LOG_LEVEL_KEY, this.loggingLevel);
        } else {
            localStorage.removeItem(HRSLoggerMonitor.LOG_LEVEL_KEY);
        }
    };

    onLog(log: NGXLogInterface): void {
        if (!this.loggingURL) return;
        if (log.level < this.ngxLoggingLevel) return;

        let logInfo: {[key: string]: any} = {
            message: log.message,
            meta: {
                environment: environment.current_environment,
                app: this.applicationName,
                userAgent: navigator.userAgent,
                hrsUserAgent: this.gatewayService.hrsUserAgentHeader,
                timestamp: log.timestamp,
                fileName: log.fileName,
                lineNumber: log.lineNumber,
                logLevel: this.levelToString[log.level]
            }
        }

        // add any additional info to the message
        log.additional.forEach((entry) => {
            if (entry) {
                if (typeof entry === 'string') {
                    logInfo.message += ` ${entry}`
                } else {
                    logInfo.message += ` ${JSON.stringify(entry)}`;
                }
            }
        });

        let headers = new HttpHeaders().append('Content-Type', 'application/json');
        let options = {headers}

        this.http.post(this.loggingURL, JSON.stringify(logInfo), options).subscribe(
            () => {},
            (err) => console.error(err)
        );
    }
}
